import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    wordTimeRange: {
      start: new Date(),
      end: new Date()
    },
    wordPage: 1,
  },
  mutations: {
    setWordTimeRange(state,value){
      state.wordTimeRange=value
    },
    setWordPage(state, value){
      state.wordPage=value;
    }
  },
  actions: {
  },
  modules: {
  }
})
