import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import About from '../views/About.vue'
import Fun from '../views/Fun.vue'
import User from '../views/User.vue'

/* Function模块 */
import { 
  FunTense,
  FunEnglishTopic,
  FunEnglishWord,
  FunMbtMen
} from '@/components/fun.js'

/* User模块 */
import {
  UserWord,
  UserFavorite,
  UserInfo
} from '@/components/user.js'
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/fun',
    name: 'Fun',
    component: Fun,
    children: [
      {
        path: 'tense',
        name: 'Tense',
        component: FunTense
      },
      {
        path: 'englishTopic',
        name: 'EnglishTopic',
        component: FunEnglishTopic,
        meta:{
          login: true
        }
      },
      {
        path: 'englishWord',
        name: 'EnglishWord',
        component: FunEnglishWord,
        meta:{
          login: true
        }
      },
      {
        path: 'mbtMen',
        name: 'MbtMen',
        component: FunMbtMen
      }
    ]
  },
  {
    path: '/user',
    name: 'User',
    component: User,
    children:[
      {
        path: 'info',
        name: 'Info',
        component: UserInfo,
        meta:{
          login: true
        }
      },
      {
        path: 'word',
        name: 'Word',
        component: UserWord,
        meta:{
          login: true
        }
      },
      {
        path: 'favorite',
        name: 'Favorite',
        component: UserFavorite,
        meta:{
          login: true
        }
      }
    ]
  },
  {
    path: '/about',
    name: 'About',
    component: About
  }
]

const router = new VueRouter({
  routes
})

export default router
