<template>
  <div>
    <div class="mdui-typo" v-if="currentData && !answerFinish">
      <div class="mdui-progress">
        <div
          class="mdui-progress-determinate"
          :style="{
            width: ((currentDataIndex + 1) / tenseListSize) * 100 + '%',
          }"
        ></div>
      </div>
      <h5>{{ tenseListData[currentDataIndex].topic }}</h5>
      <div class="mdui-textfield">
        <label class="mdui-textfield-label">请输入答案</label>
        <input
          id="answerInput"
          class="mdui-textfield-input"
          type="text"
          v-model="answer"
          @keyup.enter="verify()"
        />
      </div>
      <button
        class="mdui-btn mdui-btn-block mdui-color-theme-accent mdui-ripple"
        @click="verify()"
      >
        提交答案
      </button>
    </div>
    <div v-if="answerFinish">
      <i
        class="mdui-icon material-icons mdui-center"
        style="font-size: 100px; color: green"
        >mood</i
      >
      <div style="margin: 0 auto; text-align: center">
        <h1>答题结束</h1>
        <span>{{new Date()}}</span>
      </div>
      <div class="mdui-row-xs-2 mdui-m-a-2">
        <div class="mdui-col">
          <button
            class="mdui-btn mdui-btn-block mdui-color-theme-accent mdui-ripple"
            @click="getListData()"
          >
            再做一次
          </button>
        </div>
        <div class="mdui-col">
          <button
            class="mdui-btn mdui-btn-block mdui-color-theme-accent mdui-ripple"
            @click="$router.push('/')"
          >
            回到首页
          </button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { getListAPI, verifyAPI } from "@/apis/fun/tense.js";
import mdui from "mdui/dist/js/mdui.js";
export default {
  name: "fun-tense",
  data() {
    return {
      tenseListData: [],
      currentDataIndex: 0,
      tenseListSize: 0,
      currentData: null,
      answer: "",
      answerFinish: false,
    };
  },
  mounted() {
    this.getListData();
  },
  methods: {
    verify: function () {
      verifyAPI({
        id: this.currentData.id,
        answer: this.answer,
      }).then((res) => {
        this.answer = "";

        if (res.data.state) {
          this.currentDataIndex++;
        }

        mdui.snackbar({
          message: res.data.message,
          backgroundColor: res.data.state
            ? "mdui-color-green-600"
            : "mdui-color-deep-orange-a700",
        });
        document.getElementById("answerInput").focus();
      });
    },
    getListData: function () {
      this.tenseListData = []
      getListAPI().then((res) => {
        if (res.data.state) {
          this.tenseListData = res.data.data.data;
          this.answerFinish = false;
        }
      });
    },
  },
  watch: {
    tenseListData: function (newV) {
      if (newV != null) {
        this.tenseListSize = newV.length;
        this.currentDataIndex = 0;
        this.currentData = this.tenseListData[0];
      }
    },
    currentDataIndex: function (newV) {
      if (this.tenseListData.length > 0)
        this.currentData = this.tenseListData[newV];
      if (newV >= this.tenseListSize) this.answerFinish = true;
    },
  },
};
</script>