import http from '../http.js'

let resquest = "/user/function/english/word"

// get请求
export function getWordListAPI(uId){
    uId = Number.parseInt(uId)
    return http.get(`${resquest}/list/${uId}`)
}

export function getWordListInfoApi(uId){
    uId = Number.parseInt(uId)
    return http.get(`${resquest}/listInfo/${uId}`)
}

export function addWordApi(uid, word, meaning){
    uid = Number.parseInt(uid)
    return http.post(`${resquest}/add`,{uid:uid, word: word, meaning: meaning, _isJSON: true})
}

export function deleteWordApi(wordId){
    return http.delete(`${resquest}/delete/${wordId}`)
}

export function getFavoriteWordListApi(favoiriteId){
    return http.get(`${resquest}/listByFavorite/${favoiriteId}`)
}

export function addToFavorite(uId, word, meaning, fId){
    return http.post(`${resquest}/addToFavorite`, {
        uid: uId,
        word: word,
        meaning: meaning,
        fid: fId,
        _isJSON: true
    })
}

// 获取某月下每天状态
export function getWordRecord(dataTime){
    return http.get(`${resquest}/getWordRecord/${dataTime}`)
}

// 获取指定范围的单词
export function byDateGetList(beginDate, endDate){
    return http.get(`${resquest}/byDateGetList`, {beginDate:beginDate, endDate:endDate})
}