<template>
  <div>
    <div class="mdui-tab mdui-tab-full-width mdui-m-b-2" mdui-tab>
      <a  class="mdui-ripple" :class="{ 'mdui-tab-active': showPage == 1 }"
        @click="showPage = 1">往日记录</a>
      <a class="mdui-ripple" :class="{ 'mdui-tab-active': showPage == 2 }"
        @click="showPage = 2">添加单词</a>
    </div>
    <div v-show="showPage == 1">
      <!-- 往日记录 -->
      <vc-date-picker
        is-expanded
        :attributes="historyPage.attributes"
        v-model="historyPage.selectDated"
        :select-attribute="selectDragAttribute"
        :drag-attribute="selectDragAttribute"
        @update:to-page="changeCurrentDateValue"
        is-range
        @drag="historyPage.dragValue = $event"
        ref="historyVcPicker"
      >
        <template v-slot:day-popover="{ format }">
          <div>
            {{
              format(
                historyPage.dragValue
                  ? historyPage.dragValue.start
                  : historyPage.selectDated.start,
                "MMM D"
              )
            }}
            -
            {{
              format(
                historyPage.dragValue
                  ? historyPage.dragValue.end
                  : historyPage.selectDated.end,
                "MMM D"
              )
            }}
          </div>
        </template>
      </vc-date-picker>
    </div>
    <div v-show="showPage == 2">
      <h1>添加单词</h1>
      <div class="mdui-divider"></div>
      <div class="mdui-textfield mdui-textfield-floating-label">
        <label class="mdui-textfield-label">单词</label>
        <input
          class="mdui-textfield-input"
          id="wordInput"
          v-model="word"
          @keyup.enter="addWord(true)"
          type="text"
        />
      </div>
      <div id="menuFAZE"></div>
      <ul class="mdui-menu" id="dictListInst">
        <li
          class="mdui-menu-item"
          v-for="(dictWord, index) in dictList"
          :key="index"
        >
          <a href="javascript:;" class="mdui-ripple" @click="setWord(dictWord)"
            >{{ dictWord.key }} - {{ dictWord.paraphrase }}</a
          >
        </li>
      </ul>
      <div class="mdui-textfield mdui-textfield-floating-label">
        <label class="mdui-textfield-label">单词意思</label>
        <input
          class="mdui-textfield-input"
          v-model="meaning"
          type="text"
          @keyup.enter="addWord()"
        />
      </div>
      <div class="mdui-row-xs-2">
        <div class="mdui-col">
          <button
            class="mdui-btn mdui-btn-block mdui-color-theme-accent mdui-ripple"
            @click="addWord()"
          >
            添加
          </button>
        </div>
        <div class="mdui-col">
          <button
            class="mdui-btn mdui-btn-block mdui-color-theme-accent mdui-ripple"
            @click="clearInput()"
          >
            重置
          </button>
        </div>
      </div>
    </div>
    <button
      class="
        mdui-btn
        mdui-btn-block
        mdui-color-theme-accent
        mdui-m-t-2
        mdui-mdui-ripple
      "
      @click="goJWord"
    >
      去记单词
    </button>
    <h1>单词列表[{{ wordList.length }}]</h1>
    <span>根据日历选中的范围而列出</span>
    <div class="mdui-divider mdui-m-t-1"></div>
    <ul class="mdui-list">
      <li
        class="mdui-list-item"
        v-for="(wordItem, index) in wordList"
        :key="index"
      >
        <div class="mdui-list-item-content">
          <div class="mdui-list-item-title mdui-list-item-one-line">
            {{ wordItem.word }}
          </div>
          <div class="mdui-list-item-text mdui-list-item-two-line">
            {{ wordItem.meaning }}<br />添加时间:{{ wordItem.gmtCreate }}
          </div>
        </div>
        <i
          class="mdui-list-item-icon mdui-icon material-icons mdui-ripple"
          @click="deleteWord(index)"
          >delete</i
        >
      </li>
    </ul>
    
  </div>
</template>
<script>
import Vue from "vue";
import VCalendar from "v-calendar";
import mdui from "mdui/dist/js/mdui.js";
import {
  addWordApi,
  getWordListInfoApi,
  deleteWordApi,
  getWordRecord,
  byDateGetList,
} from "@/apis/fun/englishWord.js";
import { getIciba_DictWord } from "@/apis/external/englishWord.js";
var $ = mdui.$;
Vue.use(VCalendar, {
  componentPrefix: "vc", // Use <vc-calendar /> instead of <v-calendar />
});
export default {
  data() {
    return {
      showPage: this.$store.state.wordPage,
      historyPage: {
        currentDate: 0,
        dragValue: null,
        selectDated: this.$store.state.wordTimeRange,
        attributes: [
          {
            bar: true,
            dates: [new Date(2021, 10, 1)],
          },
        ],
      },
      id: localStorage.getItem("id"),
      noInput: false,
      word: "",
      meaning: "",
      wordList: [],
      dictList: [],
      dictListInst: null,
    };
  },
  computed: {
    selectDragAttribute() {
      return {
        popover: {
          visibility: "hover",
          isInteractive: false, // Defaults to true when using slot
        },
      };
    },
  },
  mounted() {
    mdui.mutation();
    this.byDateGetList();
  },
  methods: {
    getSomeMonth: function (someYear, someMonth) {
      getWordRecord(someYear + "-" + someMonth).then((res) => {
        res.data.data.data.forEach((element) => {
          this.historyPage.attributes[0].dates.push(
            new Date(someYear, someMonth - 1, element)
          );
        });
      });
    },
    goJWord: function () {
      if (this.wordList.length == 0) {
        mdui.alert("当前日期没有单词可记");
        return;
      }
      if (this.showPage == 1)
        this.$store.commit("setWordTimeRange", this.historyPage.selectDated);
      else
        this.$store.commit("setWordTimeRange", {
          start: new Date(),
          end: new Date(),
        });
      this.$router.push("/fun/englishWord");
    },
    setWord: function (word) {
      this.noInput = true;
      this.word = word.key;
      this.meaning = word.paraphrase;
      this.dictListInst.close();
      this.dictList = [];
    },
    clearInput: function () {
      (this.word = ""), (this.meaning = "");
    },
    getWordInfoList: function (hint = true) {
      getWordListInfoApi(this.id).then((res) => {
        if (res.data.state) {
          this.wordList = res.data.data.wordList;
        } else {
          this.wordList = [];
        }
        // 关闭查询提示
        if (false)
          mdui.snackbar({
            message: res.data.message,
            backgroundColor: res.data.state
              ? "mdui-color-green-600"
              : "mdui-color-deep-orange-a700",
          });
      });
    },
    addWord: function (entry = false) {
      if (entry) if (this.dictList.length > 0) this.setWord(this.dictList[0]);
      addWordApi(this.id, this.word, this.meaning).then((res) => {
        if (res.data.state) {
          this.clearInput();
          this.getWordInfoList(false);
          document.getElementById("wordInput").focus();
        }
        mdui.snackbar({
          message: res.data.message,
          backgroundColor: res.data.state
            ? "mdui-color-green-600"
            : "mdui-color-deep-orange-a700",
        });
      });
    },
    changeCurrentDateValue: function (date) {
      this.historyPage.currentDate = date;
      this.getSomeMonth(date.year, date.month);
    },
    deleteWord: function (wordIndex) {
      var word = this.wordList[wordIndex];
      mdui.confirm("确定要删除【" + word.word + "】单词吗？", () => {
        deleteWordApi(word.id).then((res) => {
          if (res.data.state) {
            this.getWordInfoList(false);
          }
          mdui.snackbar({
            message: res.data.message,
            backgroundColor: res.data.state
              ? "mdui-color-green-600"
              : "mdui-color-deep-orange-a700",
          });
        });
      });
    },
    byDateGetList: function (dates = null) {
      if (dates == null) dates = this.historyPage.selectDated;
      this.$store.commit("setWordTimeRange", dates);
      byDateGetList(dates.start, dates.end).then((res) => {
        this.wordList = res.data.data.list;
      });
    },
  },
  watch: {
    word: function (newV, oldV) {
      this.dictListInst = new mdui.Menu("#menuFAZE", "#dictListInst", {
        position: "bottom",
        align: "auto",
        autoWidth: true,
      });
      if (newV != oldV && newV.length > 0 && !this.noInput) {
        getIciba_DictWord(newV).then((res) => {
          this.dictList = res.data.message;
          this.dictListInst.open();
        });
      }
      this.noInput = false;
    },
    showPage: function (newV, oldV) {
      this.$store.commit("setWordPage", newV);
      if (newV == 1) {
        var date = this.historyPage.currentDate;
        this.getSomeMonth(date.year, date.month);
        this.byDateGetList();
      } else if (newV == 2) {
        this.getWordInfoList();
        document.getElementById("wordInput").focus();
        this.dictListInst = new mdui.Menu("#menuFAZE", "#dictListInst", {
          position: "bottom",
          align: "auto",
        });
      }
    },
    "historyPage.selectDated": function (newV, oldV) {
      if (newV != oldV) {
        this.byDateGetList();
      }
    },
  },
};
</script>

<style>
.huiLine {
  border: solid 1px rgb(196, 196, 196);
}
</style>