<template>
  <div>
    <div class="mdui-typo mdui-m-b-1">
      <div class="mdui-typo-title-opacity">你好！{{ userInfo.userName }}</div>
      <div class="mdui-typo-subheading-opacity">
        个性签名：{{ userInfo.explain }}
      </div>
    </div>
    <button
      class="
        mdui-btn
        mdui-btn-block
        mdui-color-theme-accent
        mdui-ripple
        mdui-btn-raised
        mdui-m-b-1
      "
      @click="outLogin"
      >退出账号</button>
    <router-link
      class="
        mdui-btn
        mdui-btn-block
        mdui-color-theme-accent
        mdui-ripple
        mdui-btn-raised
        mdui-m-b-1
      "
      to="/about"
      >关于我们</router-link
    >
    
  </div>
</template>
<script>
import { getInformationApi } from "@/apis/user/login.js";

export default {
  data() {
    return {
      userInfo: {
        id: localStorage.getItem("id"),
        userName: localStorage.getItem("userName"),
        explain: "很懒，没有介绍！",
      },
    };
  },
  mounted() {
    this.getUserInfo();
  },
  methods: {
    outLogin: function (reload = false) {
      localStorage.removeItem("token");
      localStorage.removeItem("id");
      localStorage.removeItem("userName");
      if (reload) window.location.href = "./";
    },
    getUserInfo: function () {
      getInformationApi().then((res) => {
        if (!res.data.state) {
          this.outLogin();
        } else {
          localStorage.setItem("id", res.data.data.data.id);
          localStorage.setItem("userName", res.data.data.data.userName);
          this.userInfo.id = localStorage.getItem("id");
          this.userInfo.userName = localStorage.getItem("userName");
          this.userInfo.explain = res.data.data.data.explain1;
        }
      });
    },
  },
};
</script>