import http from '../http.js'

let resquest = "/login"

// get请求
export function loginApi(email, password){
    return http.post(`${resquest}/user`,new URLSearchParams({email: email, password: password}))
}

export function registerApi(email, password, nickname, explain1){
    
    return http.post(`${resquest}/register`,{email:email, userName: nickname, password: password, explain1: explain1, _isJSON: true})
}

export function getInformationApi(){
    return http.get(`${resquest}/getInformation`)
}