<template>
  <div>
    <div class="mdui-typo" v-if="wordData != null && !answerFinish">
      <div class="mdui-progress">
        <div
          class="mdui-progress-determinate"
          :style="{
            width: (wordFinishSize / wordListSize) * 100 + '%',
          }"
        ></div>
      </div>
      <h2 class="mdui-center mdui-text-center mdui-ripple">
        {{ wordData.word }}
      </h2>
      <div class="mdui-center mdui-text-center" style="width: 100%">
        <div id="play_us" class="mdui-chip" @click="playWordAudio()">
          <span class="mdui-chip-icon mdui-color-blue"
            ><i class="mdui-icon material-icons">play_circle_filled</i></span
          >
          <span class="mdui-chip-title">美式发音</span>
        </div>
      </div>

      <div v-show="lookMean">
        <div class="mdui-divider mdui-m-t-2"></div>
        <h5>单词意思:</h5>
        <p
          class="mdui-m-l-2"
          id="meaning"
          v-html="handlerWordMeans(wordData.meaning)"
        ></p>
        <div
          id="panelLayout"
          class="mdui-row-xs-2"
          :style="'margin-top:' + (getWindowHeight() - 290) + 'px'"
        >
          <div class="mdui-col">
            <button
              class="
                mdui-btn
                mdui-btn-block
                mdui-color-theme-accent
                mdui-ripple
                mdui-btn-raised
              "
              @click="answerHandler(true)"
            >
              认识
            </button>
          </div>
          <div class="mdui-col">
            <button
              class="
                mdui-btn
                mdui-btn-block
                mdui-color-theme-accent
                mdui-ripple
                mdui-btn-raised
              "
              @click="answerHandler(false)"
            >
              不认识
            </button>
          </div>
        </div>
      </div>
      <div
        class="mdui-valign"
        v-if="lookMean == false"
        :style="'height:' + (getWindowHeight() - 225 - 45) + 'px'"
        @click="goLookMean()"
      >
        <p class="mdui-center">点击空白处查看详情</p>
      </div>
    </div>
    <div v-if="answerFinish">
      <i
        class="mdui-icon material-icons mdui-center"
        style="font-size: 100px; color: green"
        >mood</i
      >
      <div style="margin: 0 auto; text-align: center">
        <h1>答题结束</h1>
        <span>{{ new Date() }}</span>
      </div>
      <div class="mdui-row-xs-2 mdui-m-a-2">
        <div class="mdui-col">
          <button
            class="mdui-btn mdui-btn-block mdui-color-theme-accent mdui-ripple"
            @click="byDateGetList()"
          >
            再做一次
          </button>
        </div>
        <div class="mdui-col">
          <button
            class="mdui-btn mdui-btn-block mdui-color-theme-accent mdui-ripple"
            @click="$router.push('/')"
          >
            回到首页
          </button>
        </div>
      </div>
    </div>
    <div v-if="wordData == null && !answerFinish">
      <i
        class="mdui-icon material-icons mdui-center"
        style="font-size: 100px; color: green"
        >mood</i
      >
      <div style="margin: 0 auto; text-align: center">
        <h1>今天还没有添加单词哦~</h1>
        <span>{{ new Date() }}</span>
      </div>
      <div class="mdui-row-xs-2 mdui-m-a-2">
        <div class="mdui-col">
          <router-link
            class="mdui-btn mdui-btn-block mdui-color-theme-accent mdui-ripple"
            to="/user/word"
          >
            去添加单词
          </router-link>
        </div>
        <div class="mdui-col">
          <button
            class="mdui-btn mdui-btn-block mdui-color-theme-accent mdui-ripple"
            @click="$router.push('/')"
          >
            回到首页
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getWordListAPI,byDateGetList } from "@/apis/fun/englishWord.js";
import { getYouDao_Audio } from "@/apis/external/englishWord.js";
import mdui from "mdui/dist/js/mdui.js";
var $ = mdui.$;
export default {
  data() {
    return {
      userId: localStorage.getItem("id"),
      wordData: null,
      wordListSize: 0,
      wordFinishSize: 0,
      lookMean: false,
      answer: null,
      answerFinish: false,
      audioObject: new Audio(),
      countDown: null,
      dates: this.$store.state.wordTimeRange
    };
  },
  mounted() {
    this.byDateGetList();
  },
  filters: {
    handlerWordMeans: function (value) {
      if (!value) return "没有意思";
      return value.replaceAll(";", "<br/>");
    },
  },
  methods: {
    playWordAudio: function () {
      this.audioObject.play();
      this.audioObject.src = getYouDao_Audio(this.wordData.word);
      if (this.audioObject.paused) {
        this.audioObject.play();
      }
    },
    handlerWordMeans: function (value) {
      if (!value) return "没有意思";
      setTimeout((res) => {
        $("#panelLayout").css(
          "margin-top",
          this.getWindowHeight() -
            320 - 58 -
            document.getElementById("meaning").offsetHeight +
            "px"
        );
      }, 5);
      return value.replaceAll(";", "<br/>");
    },
    goLookMean: function () {
      if (!this.lookMean) this.lookMean = true;
    },
    answerHandler: function (answer) {
      // 临时变量存储
      var tempWord = this.wordData;
      var Word = {};
      var temp2Word = {
        no: this.wordData.no,
        meaning: this.wordData.meaning,
        count: this.wordData.count,
        word: this.wordData.word,
        next: Word,
      };

      // 认识
      if (answer) {
        // 判断是否已经背完
        if (this.wordData.next == null) {
          this.answerFinish = true;
          return;
        }
        //判断当前单词是否有重复记忆次数
        if (temp2Word.count != 0) {
          temp2Word.count--;
          // 查看链表有多少个单词
          var wordSize = this.fun_wordSize(this.wordData);
          this.wordData = this.wordData.next;

          if (wordSize > 5) {
            // 插入相同的单词在后第三个位置
            var insterWord = tempWord.next.next.next;
            if (insterWord == null) {
              insterWord = temp2Word;
            } else {
              // 保存插入单词后面的所有单词
              var saveWord = insterWord.next;
              insterWord.next = temp2Word;
            }
            temp2Word.next = saveWord;
          } else {
            // 插入相同的单词在后第三个位置
            var insterWord = tempWord.next;
            if (insterWord == null) {
              insterWord = temp2Word;
            } else {
              // 保存插入单词后面的所有单词
              var saveWord = insterWord.next;
              insterWord.next = temp2Word;
            }
            temp2Word.next = saveWord;
          }
          this.wordData = tempWord.next;
        } else {
          this.wordData = this.wordData.next;
          this.wordFinishSize++;
        }
      } else {
        // 忘记的次数
        var count = this.wordData.count;
        // 第一次忘记
        if (count == 0) {
          temp2Word.count = 3;
        } else if (count < 3) {
          temp2Word.count++;
        }
        // this.wordData.count != 0 要插入
        if (temp2Word.count != 0) {
          temp2Word.count--;
          // 查看链表有多少个单词
          var wordSize = this.fun_wordSize(this.wordData);

          if (wordSize > 5) {
            // 插入相同的单词在后第三个位置
            var insterWord = tempWord.next.next.next;
            if (insterWord == null) {
              insterWord = temp2Word;
            } else {
              // 保存插入单词后面的所有单词
              var saveWord = insterWord.next;
              insterWord.next = temp2Word;
            }
            temp2Word.next = saveWord;
          } else {
            // 插入相同的单词在后第三个位置
            var insterWord = tempWord.next;
            if (insterWord == null) {
              insterWord = temp2Word;
            } else {
              // 保存插入单词后面的所有单词
              var saveWord = insterWord.next;
              insterWord.next = temp2Word;
            }
            temp2Word.next = saveWord;
          }
          this.wordData = tempWord.next;
        }
      }
      this.lookMean = false;
    },
    getWindowHeight: function () {
      return window.innerHeight;
    },
    getListData: function () {
      this.wordData = null;
      this.wordListSize = 0;
      this.wordFinishSize = 0;
      this.lookMean = false;
      this.answer = null;
      this.answerFinish = false;
      getWordListAPI(this.userId).then((res) => {
        if (res.data.state) {
          this.wordData = res.data.data.data;
          this.wordListSize = this.fun_wordSize(this.wordData);
          this.wordFinishSize = 1;
        }
      });
    },
    byDateGetList: function(dates=null){
      if(dates==null)
        dates = this.dates
      this.wordData = null;
      this.wordListSize = 0;
      this.wordFinishSize = 0;
      this.lookMean = false;
      this.answer = null;
      this.answerFinish = false;
      byDateGetList(dates.start, dates.end).then(res=>{
        if (res.data.state&&res.data.data.linked.head.no!=null) {
          this.wordData = res.data.data.linked.head;
          this.wordListSize = this.fun_wordSize(this.wordData);
          this.wordFinishSize = 1;
        }
      })
    },
    fun_wordSize: function (Word) {
      // 查看单词数量
      // 从1开始,头节点有元素
      var size = 1;
      var temp = Word;
      if (temp == null) {
        return 0;
      }

      while (temp.next != null) {
        size++;
        temp = temp.next;
      }
      return size;
    },
    startJs: function () {
      if (this.countDown != null) clearTimeout(this.countDown);
      this.countDown = setTimeout((res) => {
        if (!this.lookMean)
          mdui.snackbar({
            message: "3秒了，可能记不住了哦~",
            backgroundColor: "mdui-color-red-400",
          });
        clearTimeout(this.countDown);
      }, 3000);
    },
  },
  watch: {
    wordData: function (newV) {
      if (newV != null) {
        this.playWordAudio(newV.word);
        this.startJs()
      }
    },
  },
};
</script>