import http from '../http.js'

let resquest = "/user/english/topic"

// get请求
export function getTopicListAPI(idList){
    var strList = ""
    idList.forEach(element => {
        strList+=element+","
    });
    return http.post(`${resquest}/list`,new URLSearchParams({typeIds: strList}))
}

export function verifyTopicAPI(answer){
    return http.post(`${resquest}/verify`,new URLSearchParams(answer))
}
