<template>
  <div>
    <div
      class="mdui-card"
      v-if="TopicListData == null || TopicListData.length == 0"
    >
      <div class="mdui-card-primary">
        <div class="mdui-card-primary-title">生成题目</div>
        <div class="mdui-card-primary-subtitle">
          一旦开始就不能退出了，您准备好了嘛
        </div>
      </div>
      <div class="mdui-card-content">
        <div class="mdui-row-xs-2">
          <h3>运算范围：</h3>
          <div class="mdui-col">
            <div class="mdui-textfield">
              <input
                class="mdui-textfield-input"
                type="number"
                v-model="topicMin"
                placeholder="最小值"
              />
            </div>
          </div>
          <div class="mdui-col">
            <div class="mdui-textfield">
              <input
                class="mdui-textfield-input"
                type="number"
                v-model="topicMax"
                placeholder="最大值"
              />
            </div>
          </div>
        </div>
        <div class="mdui-divider"></div>
        <div class="mdui-row-xs-2">
          <h3>题目总数与限制时间：</h3>
          <div class="mdui-col">
            <div class="mdui-textfield">
              <input
                class="mdui-textfield-input"
                type="number"
                v-model="topicTotal"
                placeholder="题目总数"
              />
            </div>
          </div>
          <div class="mdui-col">
            <div class="mdui-textfield">
              <input
                class="mdui-textfield-input"
                type="number"
                v-model="topicTime"
                placeholder="限制时间"
              />
            </div>
          </div>
        </div>
        <div class="mdui-divider"></div>
        <button
          class="mdui-btn mdui-btn-block mdui-color-theme-accent mdui-ripple"
          @click="getTopicList()"
        >
          生成题目
        </button>
      </div>
    </div>

    <div class="mdui-typo" v-if="currentData != null && !answerFinish">
      <div class="mdui-progress">
        <div
          class="mdui-progress-determinate"
          :style="{
            width: ((currentDataIndex + 1) / tenseListSize) * 100 + '%',
          }"
        ></div>
      </div>
      <h5>
        {{ TopicListData[currentDataIndex].firstNumber
        }}{{ TopicListData[currentDataIndex].operation
        }}{{ TopicListData[currentDataIndex].secondNumber }}
      </h5>
      <div class="mdui-textfield">
        <label class="mdui-textfield-label">请输入答案</label>
        <input
          id="answerInput"
          class="mdui-textfield-input"
          type="text"
          v-model="answer"
          @keyup.enter="verify()"
        />
      </div>
      <button
        class="mdui-btn mdui-btn-block mdui-color-theme-accent mdui-ripple"
        @click="verify()"
      >
        提交答案
      </button>
    </div>
    <div v-if="answerFinish">
      <i
        class="mdui-icon material-icons mdui-center"
        style="font-size: 100px; color: green"
        >mood</i
      >
      <div style="margin: 0 auto; text-align: center">
        <h1>答题结束</h1>
        <span>{{ new Date() }}</span>
      </div>
      <div class="mdui-row-xs-2 mdui-m-a-2">
        <div class="mdui-col">
          <button
            class="mdui-btn mdui-btn-block mdui-color-theme-accent mdui-ripple"
            @click="reinstated()"
          >
            再做一次
          </button>
        </div>
        <div class="mdui-col">
          <button
            class="mdui-btn mdui-btn-block mdui-color-theme-accent mdui-ripple"
            @click="$router.push('/')"
          >
            回到首页
          </button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { getTopicListAPI } from "@/apis/fun/calculate.js";
import mdui from "mdui/dist/js/mdui.js";
var timeUti;
export default {
  data() {
    return {
      TopicListData: [],
      topicTotal: "",
      topicMin: "",
      topicMax: "",
      topicTime: "",
      currentDataIndex: 0,
      tenseListSize: 0,
      currentData: null,
      answer: "",
      answerFinish: false,
    };
  },
  mounted() {

  },
  methods: {
    reinstated: function () {
      this.TopicListData = []
        this.topicTotal = 0
        this.topicMin = 0
        this.topicMax = 0
        this.topicTime = 0
        this.currentDataIndex = 0
        this.tenseListSize = 0
        this.currentData = null
        this.answer = ""
        this.answerFinish = false
        clearInterval(timeUti)
    },
    verify: function () {
      if (this.answer.length == 0) return;
      var result = 0;
      switch (this.currentData.operation) {
        case "+":
          result =
            Number.parseInt(this.currentData.firstNumber) +
            Number.parseInt(this.currentData.secondNumber);
          break;
        case "-":
          result =
            Number.parseInt(this.currentData.firstNumber) -
            Number.parseInt(this.currentData.secondNumber);
          break;
        case "*":
          result =
            Number.parseInt(this.currentData.firstNumber) *
            Number.parseInt(this.currentData.secondNumber);
          break;
      }
      var boold = result == Number.parseInt(this.answer);
    
      mdui.snackbar({
        message: boold ? "回答正确" : "回答错误",
        backgroundColor: boold
          ? "mdui-color-green-600"
          : "mdui-color-deep-orange-a700",
      });
      if(boold)
        this.currentDataIndex++
        this.answer = ""
    },
    getTopicList: function () {
      getTopicListAPI(
        this.topicMin,
        this.topicMax,
        this.topicTotal,
        this.topicTime
      ).then((res) => {
        if (res.data.state) {
          this.TopicListData = res.data.data.data;
          this.answerFinish = false;
          timeUti = setInterval(this.verify,this.topicTime*1000)
        }
      });
    },
  },
  watch: {
    TopicListData: function (newV) {
      if (newV != null) {
        this.tenseListSize = newV.length;
        this.currentDataIndex = 0;
        this.currentData = this.TopicListData[0];
      }
    },
    currentDataIndex: function (newV) {
      if (this.TopicListData.length > 0)
        this.currentData = this.TopicListData[newV];
      if (newV >= this.tenseListSize&&this.TopicListData.length > 0) this.answerFinish = true;
    },
  },
};
</script>