import request from '../externalHttp'

export function getIciba_DictWord(word){
    return request.get("https://dict.iciba.com/dictionary/word/suggestion?ck=709a0db45332167b0e2ce1868b84773e&client=6&is_need_mean=1&nums=5&timestamp=1635131036279&uid=0&word="+word+"&signature=107dee55693b43d8773281c93c3e86d3&_=1635131138729")
}

export function getYouDao_Audio(word){
    return "http://dict.youdao.com/dictvoice?audio="+word
}