<template>
  <div class="mdui-m-b-2">
    <div v-if="inputPage == 1">
      <h1>添加收藏夹</h1>
      <div class="mdui-divider"></div>
      <div class="mdui-textfield mdui-textfield-floating-label">
        <label class="mdui-textfield-label">收藏夹名字</label>
        <input
          class="mdui-textfield-input"
          id="wordInput"
          v-model="word"
          @keyup.enter="addWord(true)"
          type="text"
        />
      </div>
      <div class="mdui-row-xs-2">
        <div class="mdui-col">
          <button
            class="mdui-btn mdui-btn-block mdui-color-theme-accent mdui-ripple"
            @click="addWord()"
          >
            添加
          </button>
        </div>
        <div class="mdui-col">
          <button
            class="mdui-btn mdui-btn-block mdui-color-theme-accent mdui-ripple"
            @click="clearInput()"
          >
            重置
          </button>
        </div>
      </div>
      <button
        class="
          mdui-btn mdui-btn-block mdui-color-theme-accent mdui-ripple mdui-m-t-1
        "
        @click="inputPage = 2"
      >
        添加单词
      </button>
    </div>
    <div v-if="inputPage == 2">
      <h1>添加单词</h1>
      <div class="mdui-divider"></div>
      <select
        class="mdui-select"
        mdui-select="{position: 'bottom'}"
        v-model="favoriteCurrent"
      >
        <option
          v-for="(item, index) in favoriteList"
          :key="index"
          :value="item.id"
        >
          {{ item.collectName }}
        </option>
      </select>
      <div class="mdui-textfield mdui-textfield-floating-label">
        <label class="mdui-textfield-label">单词</label>
        <input
          class="mdui-textfield-input"
          id="wordInput"
          v-model="word"
          @keyup.enter="addWord(true)"
          type="text"
        />
      </div>
      <div id="menuFAZE"></div>
      <ul class="mdui-menu" id="dictListInst">
        <li
          class="mdui-menu-item"
          v-for="(dictWord, index) in dictList"
          :key="index"
        >
          <a href="javascript:;" class="mdui-ripple" @click="setWord(dictWord)"
            >{{ dictWord.key }} - {{ dictWord.paraphrase }}</a
          >
        </li>
      </ul>
      <div class="mdui-textfield mdui-textfield-floating-label">
        <label class="mdui-textfield-label">单词意思</label>
        <input
          class="mdui-textfield-input"
          v-model="meaning"
          type="text"
          @keyup.enter="addWord()"
        />
      </div>
      <div class="mdui-row-xs-2">
        <div class="mdui-col">
          <button
            class="mdui-btn mdui-btn-block mdui-color-theme-accent mdui-ripple"
            @click="addWord()"
          >
            添加
          </button>
        </div>
        <div class="mdui-col">
          <button
            class="mdui-btn mdui-btn-block mdui-color-theme-accent mdui-ripple"
            @click="clearInput()"
          >
            重置
          </button>
        </div>
      </div>
      <button
        class="
          mdui-btn mdui-btn-block mdui-color-theme-accent mdui-ripple mdui-m-t-1
        "
        @click="inputPage = 1"
      >
        添加收藏夹分类
      </button>
    </div>
    <h1>收藏列表</h1>
    <div class="mdui-divider"></div>
    <div class="mdui-panel mdui-panel-gapless" mdui-panel="{'accordion': true}">
      <div
        class="mdui-panel-item"
        v-for="(item, index) in favoriteList"
        :key="index"
        @click="getFavoriteWordList(item.id)"
      >
        <div class="mdui-panel-item-header">
          <div class="mdui-panel-item-title">{{ item.collectName }}</div>
          <div class="mdui-panel-item-summary">{{ item.gmtCreate }}</div>
        </div>
        <div class="mdui-panel-item-summary"></div>
        <div class="mdui-panel-item-body">
          <ul class="mdui-list">
            <li
              class="mdui-list-item"
              v-for="(wordItem, index) in wordList"
              :key="index"
            >
              <div class="mdui-list-item-content">
                <div class="mdui-list-item-title mdui-list-item-one-line">
                  {{ wordItem.word }}
                </div>
                <div class="mdui-list-item-text mdui-list-item-two-line">
                  {{ wordItem.meaning }}<br />添加时间:{{ wordItem.gmtCreate }}
                </div>
              </div>
              <i
                class="mdui-list-item-icon mdui-icon material-icons mdui-ripple"
                @click="deleteWord(index)"
                >delete</i
              >
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import mdui from "mdui/dist/js/mdui.js";
import { getFavoriteListApi, addFavoriteApi } from "@/apis/user/favorite.js";
import { getIciba_DictWord } from "@/apis/external/englishWord.js";
import {
  getFavoriteWordListApi,
  addToFavorite,
} from "@/apis/fun/englishWord.js";

export default {
  data() {
    return {
      id: localStorage.getItem("id"),
      inputPage: 1,
      noInput: false,
      word: "",
      meaning: "",
      wordList: [],
      dictList: [],
      dictListInst: null,
      favoriteList: [],
      favoriteCurrent: null,
    };
  },
  methods: {
    setWord: function (word) {
      this.noInput = true;
      this.word = word.key;
      this.meaning = word.paraphrase;
      this.dictListInst.close();
      this.dictList = [];
    },
    clearInput: function () {
      (this.word = ""), (this.meaning = "");
    },
    getFavoriteList: function () {
      getFavoriteListApi().then((res) => {
        if (!res.data.state) {
          return;
        }
        this.favoriteList = res.data.data.data;
        if (this.favoriteList.length > 0)
          this.favoriteCurrent = this.favoriteList[0].id;
        mdui.mutation();
      });
    },
    getFavoriteWordList: function (id) {
      getFavoriteWordListApi(id).then((res) => {
        if (!res.data.state) {
          return;
        }
        this.wordList = res.data.data.data;
      });
    },
    addWord: function (entry = false) {
      if (this.inputPage == 1) {
        // 添加收藏夹
        addFavoriteApi(this.word).then((res) => {
          if (!res.data.state) {
            return;
          }
          console.log(res.data);
          mdui.snackbar({
            message: res.data.message,
            backgroundColor: res.data.state
              ? "mdui-color-green-600"
              : "mdui-color-deep-orange-a700",
          });
          this.getFavoriteList();
          mdui.mutation();
        });
      } else if (this.inputPage == 2) {
        if (entry) if (this.dictList.length > 0) this.setWord(this.dictList[0]);
        addToFavorite(
          this.id,
          this.word,
          this.meaning,
          this.favoriteCurrent
        ).then((res) => {
          if (!res.data.state) {
            return;
          }
          mdui.snackbar({
            message: res.data.message,
            backgroundColor: res.data.state
              ? "mdui-color-green-600"
              : "mdui-color-deep-orange-a700",
          });
          this.getFavoriteWordList();
          mdui.mutation();
        });
      }
      this.clearInput();
    },
  },
  mounted() {
    this.getFavoriteList();
    mdui.mutation();
  },
  watch: {
    word: function (newV, oldV) {
      if (this.inputPage == 1) {
        return;
      }
      this.dictListInst = new mdui.Menu("#menuFAZE", "#dictListInst", {
        position: "bottom",
        align: "auto",
        autoWidth: true,
      });
      if (newV != oldV && newV.length > 0 && !this.noInput) {
        getIciba_DictWord(newV).then((res) => {
          this.dictList = res.data.message;
          this.dictListInst.open();
        });
      }
      this.noInput = false;
    },
  },
};
</script>
