<template>
  <div id="app">
    <header class="mdui-appbar mdui-appbar-fixed">
      <div class="mdui-toolbar mdui-color-theme">
        <a href="javascript:;" class="mdui-typo-headline">Double Day Up</a>
        <div class="mdui-toolbar-spacer"></div>
        <ul class="mdui-menu" id="userMenu">
          <li class="mdui-menu-item">
            <a href="javascript:;" class="mdui-ripple"
              >你好！{{ userInfo.userName }}</a
            >
          </li>
          <li class="mdui-menu-item">
            <router-link to="/user/word" class="mdui-ripple"
              >单词管理</router-link
            >
          </li>
          <li class="mdui-menu-item">
            <router-link to="/user/favorite" class="mdui-ripple"
              >收藏夹</router-link
            >
          </li>
          <li class="mdui-divider"></li>
          <li class="mdui-menu-item">
            <a href="javascript:;" class="mdui-ripple" @click="outLogin(true)"
              >退出</a
            >
          </li>
        </ul>
      </div>
    </header>
    <div class="mdui-container mdui-m-t-2 mdui-m-b-2">
      <router-view />
    </div>
    <div class="mdui-dialog" id="user-Dialog">
      <div class="mdui-tab mdui-tab-full-width" id="user-From">
        <a href="#user-login" class="mdui-ripple">登录账号</a>
        <a href="#user-register" class="mdui-ripple">注册账号</a>
      </div>
      <div class="mdui-dialog-content">
        <div id="user-login" class="mdui-p-a-2">
          <div class="mdui-textfield mdui-textfield-floating-label">
            <label class="mdui-textfield-label">邮箱</label>
            <input class="mdui-textfield-input" v-model="email" type="email" />
          </div>
          <div class="mdui-textfield mdui-textfield-floating-label">
            <label class="mdui-textfield-label">密码</label>
            <input
              class="mdui-textfield-input"
              v-model="password"
              type="password"
              @keyup.enter="login()"
            />
          </div>
          <div class="mdui-row-xs-2">
            <div class="mdui-col">
              <button
                class="
                  mdui-btn mdui-btn-block mdui-color-theme-accent mdui-ripple
                  dui-btn-raised
                "
                @click="login()"
              >
                登录账号
              </button>
            </div>
            <div class="mdui-col">
              <button
                class="
                  mdui-btn mdui-btn-block mdui-color-theme-accent mdui-ripple
                  dui-btn-raised
                "
                @click="celarInput()"
              >
                重置
              </button>
            </div>
          </div>
        </div>
        <div id="user-register" class="mdui-p-a-2 mdui-m-b-2">
          <div class="mdui-textfield mdui-textfield-floating-label">
            <label class="mdui-textfield-label">邮箱</label>
            <input class="mdui-textfield-input" v-model="email" type="email" />
          </div>
          <div class="mdui-textfield mdui-textfield-floating-label">
            <label class="mdui-textfield-label">昵称</label>
            <input
              class="mdui-textfield-input"
              v-model="nickname"
              type="text"
            />
          </div>
          <div class="mdui-textfield mdui-textfield-floating-label">
            <label class="mdui-textfield-label">密码</label>
            <input
              class="mdui-textfield-input"
              v-model="password"
              type="password"
            />
          </div>
          <div class="mdui-textfield mdui-textfield-floating-label">
            <label class="mdui-textfield-label">说明来历</label>
            <textarea
              class="mdui-textfield-input"
              rows="2"
              v-model="explain"
            ></textarea>
          </div>
          <div class="mdui-row-xs-2">
            <div class="mdui-col">
              <button
                class="
                  mdui-btn mdui-btn-block mdui-color-theme-accent mdui-ripple
                  dui-btn-raised
                "
                @click="register()"
              >
                注册账号
              </button>
            </div>
            <div class="mdui-col">
              <button
                class="
                  mdui-btn mdui-btn-block mdui-color-theme-accent mdui-ripple
                  dui-btn-raised
                "
                @click="celarInput()"
              >
                重置
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>

    <div class="mdui-bottom-nav mdui-color-white huiLine">
      <a
        href="javascript:void(0);"
        class="mdui-ripple mdui-ripple-white"
        :class="{ 'mdui-bottom-nav-active': $route.name == 'Home' }"
        @click="$router.push('/')"
      >
        <i class="mdui-icon material-icons">history</i>
        <label>模块</label>
      </a>
      <a
        href="javascript:void(0);"
        class="mdui-ripple mdui-ripple-white"
        :class="{ 'mdui-bottom-nav-active': $route.name == 'Word' }"
        @click="$router.push('/user/word')"
      >
        <i class="mdui-icon material-icons">book</i>
        <label>记单词</label>
      </a>
      <a
        href="javascript:void(0);"
        class="mdui-ripple mdui-ripple-white"
        :class="{ 'mdui-bottom-nav-active': $route.name == 'Favorite' }"
        @click="$router.push('/user/favorite')"
      >
        <i class="mdui-icon material-icons">library_books</i>
        <label>收藏夹</label>
      </a>
      <a
        href="javascript:void(0);"
        class="mdui-ripple mdui-ripple-white"
        :class="{ 'mdui-bottom-nav-active': $route.name == 'Setting' }"
        @click="$router.push('/user/info')"
      >
        <i class="mdui-icon material-icons">account_box</i>
        <label>我的</label>
      </a>
    </div>
  </div>
</template>

<script>
import { loginApi, registerApi, getInformationApi } from "@/apis/user/login.js";
import mdui from "mdui/dist/js/mdui.js";
var tab;
export default {
  data() {
    return {
      userInfo: {
        id: localStorage.getItem("id"),
        userName: localStorage.getItem("userName"),
      },
      email: "",
      password: "",
      nickname: "",
      explain: "",
      fabsBtn: null,
      goLogin: null,
    };
  },
  mounted() {
    this.fabsBtn = new mdui.Fab("#appFab");
    tab = new mdui.Tab("#user-From");
    var inst = new mdui.Dialog("#user-Dialog");
    document
      .getElementById("user-Dialog")
      .addEventListener("open.mdui.dialog", function () {
        tab.handleUpdate();
        inst.handleUpdate();
        mdui.mutation();
      });
    document
      .getElementById("user-From")
      .addEventListener("change.mdui.tab", function () {
        tab.handleUpdate();
        inst.handleUpdate();
        mdui.mutation();
      });
    this.goLogin = inst;
    this.getUserInfo();
  },
  methods: {
    celarInput: function () {
      this.email = "";
      this.password = "";
      this.nickname = "";
      this.explain = "";
    },
    getUserInfo: function () {
      getInformationApi().then((res) => {
        if (!res.data.state) {
          this.outLogin();
        } else {
          localStorage.setItem("id", res.data.data.data.id);
          localStorage.setItem("userName", res.data.data.data.userName);
          this.userInfo.id = localStorage.getItem("id");
          this.userInfo.userName = localStorage.getItem("userName");
        }
        if (this.$route.meta.login && !res.data.state) {
          this.$router.push("/");
          this.goLogin.open();
          mdui.snackbar({
            message: "此页面需要登录才可访问",
            backgroundColor: "mdui-color-deep-orange-a700",
          });
        }
      });
    },
    outLogin: function (reload = false) {
      localStorage.removeItem("token");
      localStorage.removeItem("id");
      localStorage.removeItem("userName");
      this.celarInput();
      if (reload) window.location.href = "./";
    },
    login: function () {
      loginApi(this.email, this.password).then((res) => {
        if (res.data.state) {
          localStorage.setItem("token", res.data.data.token);
          window.location.href = "./";
        } else {
          localStorage.removeItem("token");
          localStorage.removeItem("id");
          localStorage.removeItem("userName");
        }
        mdui.snackbar({
          message: res.data.message,
          backgroundColor: res.data.state
            ? "mdui-color-green-600"
            : "mdui-color-deep-orange-a700",
        });
      });
    },
    register: function () {
      registerApi(this.email, this.password, this.nickname, this.explain).then(
        (res) => {
          if (res.data.state) {
            tab.prev();
            mdui.mutation();
          }
          mdui.snackbar({
            message: res.data.message,
            backgroundColor: res.data.state
              ? "mdui-color-green-600"
              : "mdui-color-deep-orange-a700",
          });
        }
      );
    },
  },
  watch: {
    $route: {
      handler: function (newV) {
        this.fabsBtn.show();
        if (newV.meta.login) this.getUserInfo();
      },
      deep: true,
    },
  },
};
</script>