<template>
  <div class="home">
    <div class="mdui-row-sm-1 mdui-row-md-3 mdui-row-lg-4">
      <div
        class="mdui-col mdui-ripple mdui-ripple-white mdui-m-t-2"
        v-for="(functionItem, index) in functionList"
        :key="index"
        @click="goFun(functionItem.title)"
      >
        <div class="mdui-card">
          <div class="mdui-card-media">
            <img class=".mdui-img-rounded mdui-img-fluid" src="@/assets/card.jpg" />

            <div class="mdui-card-media-covered">
              <div class="mdui-card-primary">
                <div class="mdui-card-primary-title">
                  {{ functionItem.title }}
                </div>
              </div>
              <div class="mdui-card-content">{{ functionItem.content }}</div>
              <div class="mdui-card-actions">
                <span class="">{{ functionItem.gmtCreate }}</span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getListAPI } from "@/apis/index.js";
export default {
  data() {
    return {
      functionList: [],
    };
  },
  mounted() {
    this.getListData();
  },
  methods: {
    goFun: function (title) {
      if (title.indexOf("时态") != -1) this.$router.push("/fun/tense");
      else if (title.indexOf("英语") != -1)
        this.$router.push("/fun/englishTopic");
      else if (title.indexOf("单词") != -1)
        this.$router.push("/fun/englishWord");
      else if (title.indexOf("速算") != -1) this.$router.push("/fun/mbtMen");
    },
    getListData: function () {
      getListAPI().then((res) => {
        if (res.data.state) this.functionList = res.data.data.data;
      });
    },
  },
};
</script>