<template>
  <div>
    <div
      class="mdui-card"
      v-if="TopicListData == null || TopicListData.length == 0"
    >
      <div class="mdui-card-primary">
        <div class="mdui-card-primary-title">生成题目</div>
        <div class="mdui-card-primary-subtitle">请选择你需要的题目类型</div>
      </div>
      <div class="mdui-card-content">
        <label
          class="mdui-checkbox mdui-m-a-1"
          v-for="(item, index) in TopicTypeListData"
          :key="index"
        >
          <input type="checkbox" v-model="currentTopiced" :value="item.id" />
          <i class="mdui-checkbox-icon"></i>
          {{ item.typeName }}
        </label>
        <button
          class="mdui-btn mdui-btn-block mdui-color-theme-accent mdui-ripple"
          @click="getTopicList()"
        >
          生成题目
        </button>
      </div>
    </div>

    <div class="mdui-typo" v-if="currentData && !answerFinish">
      <div class="mdui-progress">
        <div
          class="mdui-progress-determinate"
          :style="{
            width: ((currentDataIndex + 1) / TopicListSize) * 100 + '%',
          }"
        ></div>
      </div>

      <h5>{{ currentData.topic }}</h5>
      <div class="mdui-divider"></div>
      <ul class="mdui-list" style="padding-left: 0px">
        <li
          class="mdui-list-item mdui-ripple"
          v-for="(optionItem, index) in currentData.options"
          :key="index"
          :class="{
            'mdui-color-blue-a200': answerIndex == index,
            'mdui-color-green-600': answerSuccess == index,
            'mdui-color-deep-orange-a400': answerError == index,
          }"
          @click="answerIndex = index"
        >
          {{ optionItem }}
        </li>
      </ul>
      <div class="mdui-row-xs-2">
        <div class="mdui-col">
          <button
            :disabled="currentDataIndex==0"
            class="
              mdui-btn
              mdui-btn-block
              mdui-color-theme-accent
              mdui-ripple
              mdui-btn-raised
            "
            @click="currentDataIndex--"
          >
            上一题
          </button>
        </div>
        <div class="mdui-col">
          <button
            :disabled="answerAnalysis==null"
            class="
              mdui-btn
              mdui-btn-block
              mdui-color-theme-accent
              mdui-ripple
              mdui-btn-raised
            "
            @click="currentDataIndex++"
          >
            下一题
          </button>
        </div>
      </div>
      <div v-if="answerAnalysis != null">
        <div class="mdui-divider mdui-m-t-2"></div>
        <div class="mdui-typo">
          <h5>题目解析:</h5>
          <p>{{ answerAnalysis }}</p>
        </div>
      </div>
    </div>
    <div v-if="answerFinish">
      <i
        class="mdui-icon material-icons mdui-center"
        style="font-size: 100px; color: green"
        >mood</i
      >
      <div style="margin: 0 auto; text-align: center">
        <h1>答题结束</h1>
        <span>{{ new Date() }}</span>
      </div>
      <div class="mdui-row-xs-2 mdui-m-a-2">
        <div class="mdui-col">
          <button
            class="mdui-btn mdui-btn-block mdui-color-theme-accent mdui-ripple"
            @click="reinstated()"
          >
            再做一次
          </button>
        </div>
        <div class="mdui-col">
          <button
            class="mdui-btn mdui-btn-block mdui-color-theme-accent mdui-ripple"
            @click="$router.push('/')"
          >
            回到首页
          </button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { getTopicTypeListAPI } from "@/apis/fun/englishTopicType.js";
import { getTopicListAPI, verifyTopicAPI } from "@/apis/fun/englishTopic.js";
import mdui from "mdui/dist/js/mdui.js";
export default {
  data() {
    return {
      TopicTypeListData: [],
      currentTopiced: [],
      TopicListData: [],
      currentData: null,
      currentDataIndex: 0,
      TopicListSize: 0,
      answerFinish: false,
      answerError: null,
      answerSuccess: null,
      answerIndex: null,
      answerAnalysis: null,
    };
  },
  mounted() {
    this.getTopicTypeList();
  },
  methods: {
    reinstated: function () {
      this.TopicTypeListData = [];
      this.currentTopiced = [];
      this.TopicListData = [];
      this.currentData = null;
      this.currentDataIndex = 0;
      this.TopicListSize = 0;
      this.answerError = null;
      this.answerSuccess = null;
      this.answerIndex = null;
      this.answerAnalysis = null;
      this.answerFinish = false;
      this.getTopicTypeList();
    },
    getTopicTypeList: function () {
      getTopicTypeListAPI().then((res) => {
        this.TopicTypeListData = res.data.data.data;
      });
    },
    getTopicList: function () {
      getTopicListAPI(this.currentTopiced).then((res) => {
        if (res.data.state) {
          this.TopicListData = res.data.data.data;
          this.answerFinish = false;
          if(this.TopicListData.length==0)
            mdui.snackbar({
            message: "暂时没有题目",
            backgroundColor: "mdui-color-deep-orange-a700",
          });
        }
        

      });
    },
    verifyTopic: function () {
      verifyTopicAPI({
        id: this.currentData.id,
        answer: this.currentData.options[this.answerIndex],
      }).then((res) => {
        this.answerSuccess = null;
        this.answerError = null;
        if (res.data.state) {
          this.answerSuccess = this.answerIndex;
        } else {
          this.answerError = this.answerIndex;
        }
        this.answerIndex = null;
        this.answerAnalysis = res.data.message;
      });
    },
  },
  watch: {
    TopicListData: function (newV) {
      if (newV != null) {
        this.TopicListSize = newV.length;
        this.currentDataIndex = 0;
        this.currentData = this.TopicListData[0];
      }
    },
    currentDataIndex: function (newV) {
      this.answerSuccess = null;
      this.answerError = null;
      this.answerIndex = null;
      this.answerAnalysis = null;
      if (this.TopicListData.length > 0) {
        this.currentData = this.TopicListData[newV];
        if (newV >= this.TopicListSize) this.answerFinish = true;
      }
    },
    answerIndex: function (newV) {
      if (newV != null) this.verifyTopic();
    },
  },
};
</script>